a {
  text-decoration: none;
}

button {
  cursor: pointer;
  font-size: 2rem;
  border: none;
  outline: none;
  background-color: transparent;
  color: whitesmoke;
}

.app {
  text-align: center;
}

.app-header {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  padding: 0 3rem;
  background-image: url('./images/commRetreat1718a.JPG');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: white;
}

img.logo {
  height: 4rem;
  width: 3rem;
}

.mobile-only {
  display: none;
}

.desktop-only {
  display: flex;
}

.container {
  margin: 0 1rem;
  min-width: 50vw;
}

.photo-credit {
  padding: 1rem;
  border-radius: 1rem;
  color: whitesmoke;
}

@media screen and (max-width: 600px) {
  img.logo {
    width: 3rem;
  }

  .mobile-only {
    display: flex;
  }

  .desktop-only {
    display: none;
  }

  .container {
    margin: 0 1rem;
  }

  .photo-credit {
    padding: 1rem;
    border-radius: 1rem;
    color: black;
  }
}
